.AirbnbCard-container {
    width: 250px;
    border-radius: 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: left;
        align-items: left;
    -webkit-box-shadow: 5px 3px 10px #6d6a6a;
            box-shadow: 5px 3px 10px #6d6a6a;
    margin: 1.5rem 0;
}

.AirbnbCard-image {
